.profile__name {
  font-size: 42px;
  line-height: 1.33;
  font-weight: 500;
  margin: 0 17px 0 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 390px;
}
@media screen and (max-width: 900px) {
  .profile__name {
    line-height: 2.2;
  }
}
@media screen and (max-width: 750px) {
  .profile__name {
    font-size: 27px;
    line-height: 2.07;
    text-align: center;
    margin-right: 7px;
    line-height: 2.07;
    max-width: calc(100% - 30px);
  }
}
